<template>
  <div class="text-center my-5">
    <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5 py-2"
      >最新消息</span
    >
  </div>
  <div class="container">
    <div class="row justify-content-center" v-for="item in articles" :key="item.id"
   >
      <div class="col-md-10 col-sm-12 d-md-flex my-2 shadow transform" @click="$router.push(`/articles`)">
        <img :src="item.image" class="img-fluid article-img" />
        <div class="ms-md-5">
          <h4 class="mt-4">{{ item.title }}</h4>
          <p class="fs-5 text-danger">
            活動時間 : {{ item.description }}
          </p>
          <p class="text-muted">
            發布日期 :{{ $filters.date(item.create_at) }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      articles: [],
    };
  },
  methods: {
    getArticles(page = 1) {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/articles?page=${page}`;
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.isLoading = false;
          console.log(res);
          this.articles = res.data.articles;
          this.pagination = res.data.pagination;
        }
      });
    },
  },
  created() {
    this.getArticles();
  },
};
</script>
<style lang="scss" scoped>
